.App{
  user-select: none;
}

.textCommon{
    color: #BBBBBB;    
}

.btnCommon{
    border: 2px solid #373737;
    border-radius: 5px;
    background-color: transparent;
    color: #BBBBBB;
    padding: 5px 20px;
    font-size: 1em;
    font-weight: bold;
    margin-right: 20px;
}

.btnCommonHide{
    display: none;
}
.btnCommonShow{
    display: inline;
}

.hoverElement:hover{
    cursor: pointer;
    transform: scale(1.05);
    z-index: 1000;
}

.activeElement:active{
    transform: scale(0.95);
}

.btnMarginTopDown{
    margin: 10px 0;
}

